import { db } from '../plugins/firebase'
// import $ from 'jquery'
import lightbox from 'glightbox'
let $ = require('jquery')
// import 'owl.carousel'
export default {
  data: () => ({
    layouts: [],
    page: 1,
    dialog: false,
    testimonials: [],
    stories: [],
    showProd: [],
    products: [],
    slideIndex: 1,
    contactUs: {},
    newsBgImg: {},
    productBgImg: {},
    valid: true,
    sent: false,
    name: '',
    height: '',
    nameRules: [
      v => !!v || 'Name is required',
      v => v.length > 5 || 'Name must be 4 or more characters'
    ],
    subject: '',
    subjectRules: [
      v => !!v || 'Subject is required',
      v => v.length >= 5 || 'Subject must be 4 or more characters'
    ],
    email: '',
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+/.test(v) || 'E-mail must be valid'
    ],
    message: '',
    messageRules: [
      v => !!v || 'Message is required',
      v => v.length >= 10 || 'Message must be 10 or more characters'
    ]
  }),
  created() {
    this.$bindAsObject('newsBgImg', db.ref('newsSettings'))
    this.$bindAsObject('productBgImg', db.ref('productSettings'))
    this.$bindAsArray('layouts', db.ref('homeLayout'))
    this.$bindAsArray('testimonials', db.ref('homeTestimonial'))
    this.$bindAsArray('stories', db.ref('stories'))
    this.$bindAsObject('contactUs', db.ref('contacts'))
    this.$bindAsArray('products', db.ref('products'))
    // this.$nextTick(() => {
    //   $('owl-carousel').owlCarousel({
    //     items: 1,
    //     loop: true
    //   })
    // })
    // $('.owl-carousel').owlCarousel({
    //   items: 1,
    //   loop: true
    // })
    // jquery
    $(document).ready(function () {
      $('#preloader').fadeOut('slow')
      // eslint-disable-next-line no-unused-vars
      lightbox({
        selector: 'glightbox'
      })
    })
    this.height = `${$(window).height()}px`
    $('#home').css('height', this.height)
    // eslint-disable-next-line no-undef
    var wow = new WOW()
    wow.init()
  },
  computed: {
    filterProducts () {
      if (this.products !== 0) {
        var start = (this.page - 1) * 6
        var end = start + 6
        return this.products.slice(start, end)
      }
    },
    totalPage () {
      return Math.ceil(this.products.length / 6)
    }
  },
  methods: {
    viewProduct (p) {
      this.dialog = true
      this.showProd = p
    },
    closeProduct () {
      this.dialog = false
      this.prodID = ''
    },
    currentSlide(n) {
      this.showSlides(this.slideIndex = n);
    },
    plusSlides(n) {
      this.showSlides(this.slideIndex += n);
    },
    showSlides(n) {
      var i;
      var slides = document.getElementsByClassName("work-item");
      // var dots = document.getElementsByClassName("dot");
      if (n > slides.length) {this.slideIndex = 1}
      if (n < 1) {this.slideIndex = slides.length}
      for (i = 0; i < slides.length; i++) {
          slides[i].style.display = "none";
      }
      // for (i = 0; i < dots.length; i++) {
      //     dots[i].className = dots[i].className.replace(" active", "");
      // }
      slides[this.slideIndex-1].style.display = "block";
      // dots[this.slideIndex-1].className += " active";
    },
    sendInquiry () {
      if (this.$refs.form.validate()) {
        var d = new Date()
        db.ref('inquiries').push({
          email: this.email,
          message: this.message,
          name: this.name,
          subject: this.subject,
          date: d.toUTCString()
        }).then(() => {
          this.email = ''
          this.message = ''
          this.name = ''
          this.subject = ''
          this.sent = true
          this.$refs.form.reset()
          this.$refs.form.resetValidation()
        }).catch((error) => {
          alert(error)
        })
      }
    }
  },
}