<template>
  <div>
    <v-toolbar dark fixed height="50px" @scroll="scroll" id="nav" class="animated-header">
      <v-toolbar-title class="text-uppercase">
        <a id="homebtn" @click="$vuetify.goTo('#home', goToOptions)" class="animate title text-no-wrap">Safety Tech Innovation INC.</a>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-side-icon class="hidden-md-and-up" @click="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn v-for="menu in tabs" :key="menu.name"
          flat
          @click="$vuetify.goTo(`#${menu.id}`, goToOptions)"
          target="_blank"
        >
          <span class="mr-2">{{menu.name}}</span>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-navigation-drawer
      v-model="drawer"
      app
      fixed
      dark
      temporary
    >
      <v-list class="pt-0 text-uppercase">
        <v-divider></v-divider>

        <v-list-tile
          v-for="tab in tabs"
          :key="tab.name"
          @click="$vuetify.goTo(`#${tab.id}`, goToOptions), drawer = !drawer"
        >
          <v-list-tile-action>
            <v-icon>{{ tab.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ tab.name }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
// import $ from 'jquery'
export default {
  methods: {
    scroll () {
      var e = window.scrollY
      var nav = document.getElementById("nav")
      var homebtn = document.getElementById('homebtn')
      if (e > 400) {
        homebtn.classList.add("animate")
        nav.classList.remove("animated-header")
      }
      else {
        homebtn.classList.remove("animate")
        nav.classList.add("animated-header")
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.scroll);
  },
  // mounted() {

  // },
  destroyed () {
    window.removeEventListener('scroll', this.scroll);
  },
  data: () => ({
    drawer: false,
    tabs: [
      {name: 'home', id:'home', icon: 'fa-home'},
      {name: 'news', id:'news', icon: 'fa-newspaper-o'},
      {name: 'services', id:'services', icon: 'fa-cogs'},
      {name: 'products', id:'products', icon: 'fa-fire-extinguisher'},
      {name: 'contact us', id:'contactus', icon: 'fa-phone'}
      // {name: 'our story', id:'story', icon: 'fa-history'}
    ],
    goToOptions: {
      duration: 2000,
      offset: -50,
      easing: 'easeInOutCubic'
    }
  })
}
</script>

<style>
#nav {
  -webkit-transition: all 0.8s ease 0s;
  -moz-transition: all 0.8s ease 0s;
  -ms-transition: all 0.8s ease 0s;
  -o-transition: all 0.8s ease 0s;
  transition: all 0.8s ease 0s;
  background-color: rgba(0,0,0,.4);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  padding: 5px 0;
  text-transform: uppercase;
}
#nav.animated-header {
  padding: 20px 0;
}
#homebtn.animate {
  color:rgb(255, 255, 255);
}
#homebtn {
  color:rgb(155, 156, 158);
}
#homebtn:hover {
  color: rgb(255, 255, 255);
}

</style>